<template>
  <table class="pdf-table w-100" :class="`fs-${fs_type}-18`">
    <tbody >
      <tr>
        <td width="45%">{{ $t('PDF_PRICE.quotation') }}{{ $t('PDF_PRICE.num') }}：</td>
        <td>{{ pricesNum }}</td>
      </tr> 
      <tr>
        <td>{{ $t('PDF_MSG.pay_way') }}：</td>
        <td>{{ pricesPayway }}</td>
      </tr>
      <tr>
        <td>{{ $t('PDF_MSG.pay_time') }}：</td>
        <td>{{ pricesPayTime }}</td>
      </tr>
      <tr>
        <td>{{ $t('PDF_PRICE.quotate') }}{{ $t('PDF_LISTING.date') }}：</td>
        <td>{{ pricesTime }}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
export default {
  name: 'PdfPriceMsg',
  props: {
    pricesTime: {
      type: String
    },
    pricesNum: {
      type: String,
      default: ''
    },
    pricesPayTime: {
      type: String,
      default: ''
    },
    pricesPayway: {
      type: String
    },
    member: {
      type: Object
    },
    fs_type: {
      type: String,
      default: 'em'
    }
  },
  data() {
    return {
      
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"
</style>


